import Vue from 'vue';
import Router from 'vue-router';

import Home from './views/Home.vue';
import Navigation from './views/Navigation.vue';
import NavGet from './views/NavGet.vue';
import NavAnchor from './views/NavAnchor.vue';
import NavForm from './views/NavForm.vue';
import NavHistory from './views/NavHistory.vue';
import NavScript from './views/NavScript.vue';
import Wait from './views/Wait.vue';
import WaitImplicit from './views/WaitImplicit.vue';
import WaitAlert from './views/WaitAlert.vue';
import WaitClickable from './views/WaitClickable.vue';
import WaitElSelected from './views/WaitElSelected.vue';
import WaitElSelection from './views/WaitElSelection.vue';
import WaitElLocatorSlected from './views/WaitElLocatorSelected.vue';
import WaitElLocatorSelection from './views/WaitElLocatorSelection.vue';
import WaitFrame from './views/WaitFrame.vue';
import WaitInvisible from './views/WaitInvisibility.vue';
import WaitInvisibilityLocator from './views/WaitInvisibilityLocator.vue';
import WaitNewWindow from './views/WaitNewWindow.vue';
import WaitNumWindow from './views/WaitNumWindow.vue';
import WaitPresenceAll from './views/WaitPresenceAll.vue';
import WaitPresenceOne from './views/WaitPresenceOne.vue';
import WaitStateless from './views/WaitStateless.vue';
import WaitText from './views/WaitText.vue';
import WaitTextValue from './views/WaitTextValue.vue';
import WaitTitle from './views/WaitTitle.vue';
import WaitTitleContains from './views/WaitTitleContains.vue';
import WaitUrlChange from './views/WaitURLChange.vue';
import WaitUrlContains from './views/WaitURLContains.vue';
import WaitUrlIs from './views/WaitURLIs.vue';
import WaitUrlMatches from './views/WaitURLMatches.vue';
import WaitVisibility from './views/WaitVisibility.vue';
import WaitVisibilityAll from './views/WaitVisibilityAll.vue';
import WaitVisibilityAny from './views/WaitVisibilityAny.vue';
import WaitVisibilityOne from './views/WaitVisibilityOne.vue';
import WaitCustom from './views/WaitCustom.vue';
import Locator from './views/Locator.vue';
import LocatorClassName from './views/LocatorClassName.vue';
import LocatorCSSSelector from './views/LocatorCSSSelector.vue';
import LocatorId from './views/LocatorId.vue';
import LocatorLinkText from './views/LocatorLinkText.vue';
import LocatorName from './views/LocatorName.vue';
import LocatorPartialLinkText from './views/LocatorPartialLinkText.vue';
import LocatorTagName from './views/LocatorTagName.vue';
import LocatorXpath from './views/LocatorXpath.vue';
import FindElement from './views/FindElement.vue';
import FindElByClassName from './views/FindElByClassName.vue';
import FindElByCSSSelector from './views/FindElByCSSSelector.vue';
import FindElById from './views/FindElById.vue';
import FindElByLinkText from './views/FindElByLinkText.vue';
import FindElByName from './views/FindElByName.vue';
import FindElByPartialLinkText from './views/FindElByPartialLinkText.vue';
import FindElByTagName from './views/FindElByTagName.vue';
import FindElByXpath from './views/FindElByXpath.vue';
import NotFound from './views/NotFound.vue';

Vue.use(Router);

export default new Router({
    mode: 'history',
    scrollBehavior() {
        return { x: 0, y: 300 }
    },
    routes: [
        { path: '/', component: Home },
        { path: '/nav', component: Navigation },
        { path: '/navget', component: NavGet },
        { path: '/navanchor', component: NavAnchor },
        { path: '/navform', component: NavForm },
        { path: '/navhistory', component: NavHistory },
        { path: '/navscript', component: NavScript },
        { path: '/wait', component: Wait },
        { path: '/waitimplicit', component: WaitImplicit },
        { path: '/waitalert', component: WaitAlert },
        { path: '/waitclickable', component: WaitClickable },
        { path: '/waitelselected', component: WaitElSelected },
        { path: '/waitelselection', component: WaitElSelection },
        { path: '/waitellocatorselected', component: WaitElLocatorSlected },
        { path: '/waitellocatorselection', component: WaitElLocatorSelection },
        { path: '/waitframe', component: WaitFrame },
        { path: '/waitinvisible', component: WaitInvisible },
        { path: '/waitinvisibilitylocator', component: WaitInvisibilityLocator },
        { path: '/waitnewwindow', component: WaitNewWindow },
        { path: '/waitnumwindow', component: WaitNumWindow },
        { path: '/waitpresenceall', component: WaitPresenceAll },
        { path: '/waitpresenceone', component: WaitPresenceOne },
        { path: '/waitstateless', component: WaitStateless },
        { path: '/waittext', component: WaitText },
        { path: '/waittextvalue', component: WaitTextValue },
        { path: '/waittitle', component: WaitTitle },
        { path: '/waittitlecontains', component: WaitTitleContains },
        { path: '/waiturlchange', component: WaitUrlChange },
        { path: '/waiturlcontains', component: WaitUrlContains },
        { path: '/waiturlmatches', component: WaitUrlMatches },
        { path: '/waiturlis', component: WaitUrlIs },
        { path: '/waitvisibility', component: WaitVisibility },
        { path: '/waitvisibilityall', component: WaitVisibilityAll },
        { path: '/waitvisibilityany', component: WaitVisibilityAny },
        { path: '/waitvisibilityone', component: WaitVisibilityOne },
        { path: '/waitcustom', component: WaitCustom },
        { path: '/locator', component: Locator },
        { path: '/locatorclassname', component: LocatorClassName },
        { path: '/locatorcssselector', component: LocatorCSSSelector },
        { path: '/locatorid', component: LocatorId },
        { path: '/locatorlinktext', component: LocatorLinkText },
        { path: '/locatorname', component: LocatorName },
        { path: '/locatorpartiallinktext', component: LocatorPartialLinkText },
        { path: '/locatortagname', component: LocatorTagName },
        { path: '/locatorxpath', component: LocatorXpath },
        { path: '/findel', component: FindElement },
        { path: '/findelbyclassname', component: FindElByClassName },
        { path: '/findelbycssselector', component: FindElByCSSSelector },
        { path: '/findelbyid', component: FindElById },
        { path: '/findelbylinktext', component: FindElByLinkText },
        { path: '/findelbyname', component: FindElByName },
        { path: '/findelbypartiallinktext', component: FindElByPartialLinkText },
        { path: '/findelbytagname', component: FindElByTagName },
        { path: '/findelbyxpath', component: FindElByXpath },
        { path: '/*', component: NotFound },
    ]
});

